@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{
  font-family:'digital';
  src: url(/CalculatorOfTheOld/static/media/digital-7.regular.1e670d88.ttf);
 }

.App {
  text-align: center;
 
}
.container{
  width: 330px;
  height: 500px;
  margin: 40px auto;
}
#calculator{
  display: grid;
  height: 400px;
  background-color: aquamarine;
  border: 3px solid black;

  
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows:  30px auto ;
  grid-template-areas: 
  "outputScreen outputScreen outputScreen outputScreen"
  "AC AC slash x"
  "seven eight nine minus"
  "four five six plus"
  "one two three equal"
  "zero zero point equal";
}
button{
  color: white;
  font-size: larger;
  background-color:#4D4D4D ;
  overflow: visible;
  border: 1px solid black;
  outline:none;  
  font-family: "Share Tech Mono", monospace;
}
button:hover{
  color: black;
  outline: .05em solid grey;
  z-index: 3;
}

.AC{
  grid-area: AC;
  background-color: brown;
}
p{
 
}
.formulaScreen{
  
  background-color: black;
  font-family: digital;
  color:yellow;
  text-align: right;
  
  font-size: larger;
  overflow-wrap:break-word;
  margin:0px;
  padding:3px 5px 0px;
  align-self:center;
  min-height: 30px;
  
  
}
.outputScreen{
  grid-area: outputScreen;
  background-color: black;
  font-size: xx-large;
  font-family: digital;
  color:white;
  text-align: right;
  
  overflow-wrap:break-word;
  margin:0px;
  padding:0px;
  align-self:center;
  height:100%
  
  
}
.slash{
  grid-area: slash;
  background-color: #666666;
}
.one{
  grid-area: one
}
.two{
  grid-area: two
}
.three{
  grid-area: three
}
.four{
  grid-area: four
}
.five{
  grid-area: five
}
.six{
  grid-area: six
}
.seven{
  grid-area: seven
}
.eight{
  grid-area: eight
}
.nine{
  grid-area: nine
}
.ten{
  grid-area: ten
}
.point{
  grid-area: point
}
.equal{
  grid-area: equal;
  background-color: #004466;
}
.zero{
  grid-area: zero
}
.plus{
  grid-area: plus;
  background-color: #666666;
}
.minus{
  grid-area: minus;
  background-color: #666666;
}
.x{
  grid-area: x;
  background-color: #666666;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

